@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Montserrat:500");
@import url(https://fonts.googleapis.com/earlyaccess/mplus1p.css);
html {
  width: 100%;
  height: 100%;
  font-size: 62.5%; }

body {
  color: #333333;
  font-family: 'Century Gothic', 'Montserrat', 'Mplus 1p', 'メイリオ', 'osaka', sans-serif;
  padding: 0;
  margin: 0;
  font-size: 14px;
  font-size: 1.4rem; }

h1, h2, h3, h4, h5, p, ul, li, div, section, article {
  margin: 0;
  padding: 0; }

.wrapper {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  position: relative; }

/**/
.content-header {
  background: #004015;
  color: #fff;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: vertical;
  -moz-flex-direction: vertical;
  -ms-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 240px;
  height: 100vh;
  align-items: center;
  z-index: 2; }
  .content-header .myname {
    display: none; }
  .content-header span {
    margin: auto auto 2.5rem; }
    .content-header span img {
      margin: 0;
      box-sizing: border-box;
      padding: 0;
      border-radius: 100%;
      max-width: 13rem;
      max-height: 13rem;
      border: 0.6rem solid #14a243; }

.title {
  font-size: 20px;
  font-size: 2rem;
  text-align: center;
  margin: auto auto 2.5rem;
  padding: 0; }

.content-nav {
  margin-bottom: auto; }
  .content-nav .content-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: block;
    font-size: 20px;
    font-size: 2rem;
    text-align: center; }
    .content-nav .content-menu li {
      margin-bottom: 2rem; }
      .content-nav .content-menu li a {
        color: #ffffff;
        text-decoration: none;
        transition: .2s;
        display: block;
        opacity: 0.5;
        filter: alpha(opacity=50); }
        .content-nav .content-menu li a:hover, .content-nav .content-menu li a.active {
          opacity: 1;
          filter: alpha(opacity=100); }

