/**/
footer {
  display: block;
  position: fixed;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.75);
  width: 100%;
  z-index: 1; }
  footer .copyright {
    font-size: 14px;
    font-size: 1.4rem;
    padding: 0.5rem 1.5rem .5rem;
    text-align: right; }

